import React, { Component } from "react";
import * as actions from "../actions";
import { connect } from "react-redux";
import "./login.scss";

class LoginForm extends Component {
  state = {
    wrongPasswordAlert: "",
    username: "",
    password: ""
  };
  title = "Socializer";
  usernameRef = null;
  passwordRef = null;
  login(e) {
    e.preventDefault();
    this.setState({
      loginError: "",
      wrongPasswordAlert: "none"
    });
    this.props.dispachLogin({
      username: this.state.username.toLowerCase(),
      password: this.state.password
    });
    // v-arjjos@microsoft.com
    // setTimeout(() => {
    //   if (this.state.username === "anubhav" && this.state.password === "a") {
    //     this.setState({
    //       loading: false,
    //       wrongPasswordAlert: "none"
    //     });
    //   } else {
    //     this.setState({
    //       loading: false,
    //       loginError: "Wrong Password",
    //       wrongPasswordAlert: "block"
    //     });
    //   }
    //   //       this.router.navigate(['/home']);
    // }, 3000);
    // this._authenticateService.login(this.model.username, this.model.password)
    //   .subscribe(resp => {
    //     console.log(resp);
    //     console.log(resp.message);
    //     if (resp.response !== 'success') {
    //       this.loginError = resp.message;
    //       this.wrongPasswordAlert = 'block';
    //     } else {
    //       this.wrongPasswordAlert = 'none';
    //       this.router.navigate(['/home']);
    //     }
    //     this.loading = false;
    //     // this.router.navigate(['/home']);
    //   }, err => {
    //     console.log(err);
    //     this.loginError = ' Server Error ';
    //     this.loading = false;
    //   });
  }
  render() {
    return (
      <div className="col-lg-10 mx-auto">
        <div className="row">
          <div className="col-md-6">
            <h1>Welcome to {this.title}</h1>
          </div>
          <div className="col-md-6">
            <div className="row">
              <form
                name="form"
                className="col-lg-10 mx-auto"
                onSubmit={this.login.bind(this)}
                noValidate
              >
                <div
                  className={`form-group col-12 ${
                    this.usernameRef &&
                    this.usernameRef.value.length > 0 &&
                    !this.usernameRef.checkValidity()
                      ? ""
                      : "has-error"
                  }`}
                >
                  <input
                    type="text"
                    ref={el => {
                      this.usernameRef = el;
                    }}
                    className="form-control lowercase"
                    placeholder="username"
                    name="username"
                    value={this.state.username}
                    onChange={e => {
                      this.setState({ username: e.target.value });
                    }}
                    required
                  />
                  {this.usernameRef &&
                    this.usernameRef.value.length > 0 &&
                    !this.usernameRef.checkValidity() && (
                      <div className="help-block error">
                        Username is required
                      </div>
                    )}
                </div>
                <div
                  className={`form-group col-12 ${
                    this.passwordRef &&
                    this.passwordRef.value.length > 0 &&
                    !this.passwordRef.checkValidity()
                      ? ""
                      : "has-error"
                  }`}
                >
                  <input
                    type="password"
                    ref={el => {
                      this.passwordRef = el;
                    }}
                    className="form-control"
                    placeholder="password"
                    name="password"
                    value={this.state.password}
                    onChange={e => {
                      this.setState({ password: e.target.value });
                    }}
                    required
                  />
                  {this.passwordRef &&
                    this.passwordRef.value.length > 0 &&
                    !this.passwordRef.checkValidity() && (
                      <div className="help-block error">
                        Password is required
                      </div>
                    )}
                </div>
                {this.props.loginError && this.props.loginError.length > 0 && (
                  <div
                    styles={`display:${this.state.wrongPasswordAlert}`}
                    className="form-group alert alert-danger col-10 mx-auto"
                    role="alert"
                  >
                    {this.props.loginError}
                  </div>
                )}
                <div className="form-group">
                  <button
                    type="submit"
                    disabled={this.props.loading}
                    className="btn btn-primary"
                  >
                    Login
                    {this.props.loading && (
                      <img
                        alt=""
                        src="data:image/gif;base64,"
                      />
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapPropsToStore = ({ Authentication }) => {
  return {
    loginError: (Authentication && Authentication.loginError) || "",
    loading: (Authentication && Authentication.loading) || false
  };
};
const mapDispatchToProps = dispach => {
  return {
    dispachLogin: payload => dispach({ type: actions.LOGIN, payload })
  };
};
export default connect(
  mapPropsToStore,
  mapDispatchToProps
)(LoginForm);
